body {
  position: relative;
  margin: 0;
  min-height: 100vh;
  padding-right: 0 !important;
  color: #fff;
  font-size: 16px;
  line-height: 1.2;
  font-weight: 400;
  font-family: 'Proxima Nova', Fallback, sans-serif;
  background: #232e34;
  overflow-y: scroll;
}
body::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #F5F5F5;
}
body::-webkit-scrollbar {
  width: 6px;
  background-color: #F5F5F5;
}
body::-webkit-scrollbar-thumb {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #0b355b;
}
*::selection {
  background-color: #6b6c70;
  color: #fff;
}
section {
  padding: 60px 0;
}
.section__title {
  position: relative;
  display: table;
  font-size: 40px;
  font-weight: 700;
  text-align: center;
  margin: auto;
}
.section__title:before {
  content: '';
  display: block;
  position: absolute;
  left: -95px;
  top: 50%;
  transform: translateY(-50%);
  width: 81px;
  height: 2px;
  background: #aeafaf;
}
.section__title:after {
  content: '';
  display: block;
  position: absolute;
  right: -108px;
  top: 70%;
  transform: translateY(-50%);
  width: 94px;
  height: 24px;
  background: url('../images/icons/decor-title.png') no-repeat;
}
::-webkit-input-placeholder {
  font-size: 16px;
}
::-moz-placeholder {
  font-size: 16px;
}
:-ms-input-placeholder {
  font-size: 16px;
}
:-moz-placeholder {
  font-size: 16px;
}
.header {
  position: relative;
  background: url(../images/hero-back.jpg) no-repeat center / cover;
}
.header:after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 50%;
  transform: translateX(-50%);
  width: 164px;
  height: 50px;
  background: url(../images/decor-section-header.png) no-repeat;
}
.header__top {
  padding: 10px 0;
  background: #0b355b;
}
.header__box {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__item {
  margin-right: 35px;
  list-style: none;
}
.header__link {
  position: relative;
  padding-bottom: 5px;
}
.header__link:after {
  content: '';
  position: absolute;
  bottom: 0px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #abc12e;
  transition: transform .3s  linear;
  transform: scaleX(0);
  transform-origin: bottom right;
  z-index: 10;
}
.header__link:hover {
  transition: all .5s;
}
.header__link:hover:after {
  -webkit-transform-origin: bottom left;
  -ms-transform-origin: bottom left;
  transform-origin: bottom left;
  transform: scaleX(1);
}
.header__phone {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 180px;
}
.header__center {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 860px;
}
.header__title {
  padding-bottom: 25px;
  font-size: 52px;
  line-height: 1;
  font-weight: 700;
}
.header__subtitle {
  font-size: 30px;
}
.header .btn {
  margin: 25px auto;
}
