.reason {
  position: relative;
  background: #fff;
  color: #000;
}
.reason:after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 50%;
  transform: translateX(-50%);
  width: 165px;
  height: 50px;
  background: url(../images/decor-section-about.png) no-repeat;
}
.reason__list {
  padding-top: 80px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.reason__item {
  counter-increment: li;
  padding-left: 25px;
  padding-right: 25px;
  margin-bottom: 115px;
  flex-basis: 30%;
  border-left: 2px solid #0b355b;
  list-style-type: none;
}
.reason__item .reason__title:before {
  content: "" counter(li);
  position: absolute;
  left: -90px;
  top: 0;
  width: 64px;
  height: 54px;
  background: #0b355b;
  color: #fff;
  font-size: 54px;
  line-height: 54px;
  font-family: 'Proxima Nova', Fallback, sans-serif;
  font-weight: 400;
  text-align: center;
}
.reason__title {
  position: relative;
  padding-bottom: 10px;
  font-size: 23px;
  font-weight: 700;
  color: #415b72;
  text-transform: uppercase;
}
.reason__text {
  font-size: 17px;
  font-weight: 300;
}
.proposal {
  position: relative;
  background: #e8e8e8;
  color: #000;
}
.proposal .wrapper {
  position: relative;
}
.proposal__box {
  padding: 120px;
  display: flex;
  justify-content: center;
  overflow: hidden;
}
.proposal__title {
  padding-bottom: 10px;
  margin-bottom: 10px;
  position: relative;
  font-size: 40px;
  font-weight: 700;
}
.proposal__title:before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 258px;
  height: 11px;
  background: url('../images/icons/css_sprites.png') -10px -10px;
}
.proposal__text {
  padding-bottom: 20px;
  font-size: 18px;
  font-weight: 300;
}
.proposal__accent {
  font-size: 20px;
  font-weight: 700;
}
.proposal__list {
  padding-left: 23px;
  margin: 15px 0;
  font-size: 18px;
  font-weight: 300;
}
.proposal__photo {
  position: relative;
  padding-left: 80px;
}
.proposal__img {
  position: relative;
  width: 790px;
  z-index: 10;
}
.proposal__img img {
  box-shadow: 6.103px 11.478px 13px 0px rgba(0, 0, 0, 0.32);
}
.proposal__decor-left {
  position: absolute;
  top: 160px;
  left: 50px;
}
.proposal__decor-right {
  position: absolute;
  top: -120px;
  left: 120px;
  z-index: 0;
}
.proposal abbr {
  text-decoration: none;
}
.proposal .btn {
  color: #fff;
}
.video {
  position: relative;
  background: url(../images/video-back.jpg) no-repeat center / cover;
  color: #000;
  box-shadow: 6.103px 11.478px 13px 0px rgba(0, 0, 0, 0.32);
  z-index: 10;
}
.video__box {
  max-width: 900px;
  margin: 30px auto;
}
.video .video-responsive {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
}
.video .video-responsive iframe,
.video .video-responsive object,
.video .video-responsive embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.step {
  position: relative;
  z-index: 1;
  background: url(../images/step-back.jpg) no-repeat center / cover;
  color: #000;
}
.step:after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 50%;
  transform: translateX(-50%);
  width: 165px;
  height: 50px;
  background: url(../images/decor-section-about.png) no-repeat;
}
.step__decor {
  position: absolute;
  top: 160px;
  right: 0;
}
.step__text {
  padding: 30px 0;
  font-size: 18px;
  font-weight: 300;
  text-align: center;
}
.step__box {
  position: relative;
  display: flex;
  justify-content: center;
}
.step__title {
  position: absolute;
  top: 330px;
  left: 44%;
  text-align: center;
  font-size: 18px;
}
.step__item {
  position: absolute;
  list-style: none;
  text-align: center;
  font-size: 16px;
}
.step__item:before {
  content: '';
  position: absolute;
  display: block;
  width: 155px;
