@keyframes arrow-bounce {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-6px);
  }
  100% {
    transform: translateY(0px);
  }
}
@keyframes smoothScroll {
  0% {
    opacity: 0;
    transform: translateY(-40px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}
@keyframes antiSmoothScroll {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-40px);
  }
}
@keyframes pulsed {
  0% {
    /* box-shadow: 0 0 0 0 rgba(204,169,44, 0.4); */
    transform: scale(1);
  }
  70% {
    /* box-shadow: 0 0 0 10px rgba(204,169,44, 0); */
    transform: scale(1.05);
  }
  100% {
    /* box-shadow: 0 0 0 0 rgba(204,169,44, 0); */
    transform: scale(1);
  }
}
@keyframes growLine {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}
.slick-prev,
.slick-next {
  width: 50px;
  height: 100%;
  opacity: 1;
  transition: opacity .3s linear;
  z-index: 10;
}
.slick-prev {
  top: 152px;
  left: -50px;
}
.slick-next {
  top: 213px;
  left: -50px;
}
.slick-prev:before,
.slick-next:before {
  width: 0;
  height: 0;
}
.slick-prev:focus,
.slick-next:focus {
  opacity: 1;
}
.slick-disabled {
  opacity: .35!important;
}
.slick-dots {
  display: inline-block !important;
  left: -48px;
  top: 50%;
  transform: translateY(17%);
  border-image: transparent;
}
.slick-dots li {
  margin: 4px 8px;
  margin-bottom: 10px;
  width: 13px;
  height: 13px;
}
.slick-dots li button {
  background: #fff;
  display: block;
  height: 13px;
  width: 13px;
  font-size: 0px;
  color: #fff;
  border-radius: 50%;
  box-shadow: 0px 5px 7px 1px rgba(0, 45, 86, 0.15);
  opacity: 0.75;
}
.slick-dots li button:before {
  font-size: 0;
  opacity: 0;
  display: none;
}
.slick-dots li.slick-active button {
  background: #fb0035;
  border-radius: 50%;
  opacity: 1;
}
@media only screen and (min-width: 768px) {
  .slick-dots li.slick-active button {
    background: #fff;
  }
}
.slick-current,
.slick-slide,
.slick-active {
  outline: none!important;
}
